

export const next = (ms, callback) => {
  return new Promise((resolve) => {
    setTimeout(() => {
      callback();
      resolve(true);
    }, ms);
  });
};

export const pause = (ms) => {
  return new Promise((resolve) => {
    setTimeout(() => {
      resolve(true);
    }, ms);
  });
};


export const log = (...args) => {
  if (debugLevel == 0) return;
  console.log(...args);
};

export const randomRange = (min, max) => {
  return (Math.random() * (max - min)) + min;
}
